import React from 'react'
import './Card.css'
import {ComboPerfil} from './ComboPerfil';
export const Tabladata = ({data,columns}) => {
  return (
    <div className='margin-div'>
      <ComboPerfil/>
      <table className='customers'>
        <thead>
          <tr>
            {
              columns.map(({title})=>{
                return (<th>{title}</th>)
              })
            }
          </tr>
        </thead>
        <tbody>
          {
            data.map((data)=>{
              return(
              
              <tr>
                <td>{data.id}</td>
                <td>{data.nombre}</td>
                <td>{data.fecha}</td>
                <td><a href={`/detail/${data.id}/${data.nombre}`}>Ver detalle</a></td>
                </tr>
              )
            })
          }
          </tbody>
        
      </table>
    </div>
  )
}