import React from 'react'
import DropdownList from "react-widgets/DropdownList";

export const ComboPerfil = () => {
  const colors = [
    { id: 0, name: 'orange'},
    { id: 1, name: 'purple'},
    { id: 2, name: 'red' },
    { id: 3, name: 'blue' },
  ];
  return (
    <>
      <DropdownList
        data={colors}
        dataKey='id'
        textField='name'
        defaultValue={1}
      />
    </>
  )
}

